<template>
  <div id="app">
    <video @ended="ended" @canplay="canplay" ref="video" class="video" v-if="!isLoading" src="http://mp4.vjshi.com/2018-05-05/bb349cb96811b1b32359f3bc88cf8b66.mp4" controls="controls"></video>
    <loading v-if="isLoading"></loading>
    <cell @select="selectItem"></cell>
    <bg-mask :mask="mask"></bg-mask>
  </div>
</template>

<script>
import loading from '@/components/loading'
import cell from '@/components/cell'
import bgMask from '@/components/mask'
export default {
  name: 'App',
  data(){
    return{
      isLoading:false,
      mask:''
    }
  },
  methods:{
    canplay(){
      //this.isLoading = true;
    },
    ended(){
      this.mask = 'dialog'
    },
    selectItem(item){
      this.$refs.video.pause()
      this.$refs.video.src = item.video
      this.$refs.video.play()
    }
  },
  components:{
    loading,
    cell,
    bgMask
  }
}
</script>

<style>

</style>



