<template>
    <div class="mask">
        <div class="weui-mask" v-if="mask==='dialog'">
            <div class="weui-dialog">
                <div class="weui-dialog__hd"><i class="weui-loading"></i></div>
                <div class="weui-dialog__bd">
                    <h3>数据加载中断</h3>
                    <p>请分享到<span>微信群</span>，可<span>免流量加速观看</span></p>
                </div>
                <div class="weui-dialog__ft">
                    <a href="#" class="weui-dialog__btn weui-dialog-btn__primary" @click="changemask">立即分享</a>
                </div>
            </div>
        </div>
    <div class="bg-mask" v-if="mask==='bgmask'" @click="changebgmask"></div>
    </div>
</template>

<script>
export default {
    props:{
        mask:{
            type:String,
            default:''
        }
    },
    methods:{
        changemask(){
            this.mask = 'bgmask'
        },
        changebgmask(){
            this.mask = ''
        }
    }
}
</script>

<style>

</style>



